.footer {
  width: 100%;
  height: 40px;
  background-color: rgb(64, 64, 64);
  position: fixed;
  bottom: 0px;
  display: flex;
  justify-content: center;
  align-content: center;
  list-style: none;
}

.btnFooter {
  border-style: none;
  background-color: transparent;
  color: rgb(231, 231, 231);
  font-family: "Oswald", sans-serif;
  font-size: 11px;
  margin-left: 10px;
  margin-right: 10px;
  /* z-index: 1; */
}

.btnFooter a {
  font-size: 14px;
  color: white;
}

.btnFooter a:hover {
  font-size: 14px;
  color: rgb(204, 204, 204);
  text-decoration: none;
}

ul li a {
  font-size: 8px;
  color: white;
}
