.main {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.561);
}

h1 {
  font-family: "Oswald", sans-serif;
  font-weight: 300;
  /* font-family: "Sansita Swashed", cursive; */
  color: #404040;
  font-size: 38px;
  margin-bottom: 10px;
}

h1::after {
  content: "";
  border: 1px solid #404040;
  display: block;
}

.logo {
  width: 61px;
  height: auto;
}

p {
  font-family: "Oswald", sans-serif;
  color: #404040;
  font-size: 15px;
  text-align: center;
}

.btnJoinFind {
  width: 42px;
  height: 22px;
  font-size: 9px;
  color: white;
  background-color: rgb(183, 60, 7);
  border: none;
  margin-top: -1px;
}

.btnJoin:hover {
  background-color: #272727;
}