.dropdown-menu {
    max-width: 50px;
    min-width: 100px;
    width: 100px !important;
}

.menu {
    display: flex;
    flex-direction: row;
    margin-left: 10px;
    margin-right: 10px;
    align-items: center;
}

.DropDownMenu {
    display: flex;
    flex-direction: row;
    height: 35px;
    align-content: center;
    justify-items: center;
    justify-content: center;

}

.inp {
    height: 23px;
    font-size: 12px;
    width: 90px;
    font-style: italic;
}

.btnCat:hover {
    color: #162b32;
}

.btnCat {
    font-size: 12px;
    border-width: 1px;
    border-radius: 0px;
    height: 23px;
}

.btnFind {
    margin-top: 6px;
    width: 42px;
    height: 22px;
    font-size: 12px;
    color: white;
    background-color: rgb(183, 60, 7);
    border: none;
}