.about {
    /*  min-height: 100vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    background: var(--clr-grey-light);
    color: var(--clr-grey-dark);
    margin-top: 150px;
    /* margin-bottom: 30px; */
    /* z-index: -1;
    grid-area: about; */
}

h4 {
    font-family: 'Oswald', sans-serif;
}

h5 {
    font-family: 'Oswald', sans-serif;
    font-size: 2.5rem;
    margin-top: 1rem;
    color: var(--clr-orange-dark);
}

.card-deck {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 3.5rem;
    width: 100vw;
}

.card {
    width: 35%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    margin-left: 0.2rem;
    border: 2px solid black;
}

.card:hover {
    background: var(--clr-orange-light);
    /* opacity: 0.95;  */
    cursor: pointer;
    color: var(--clr-white);
}

.card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 2rem;
}

.card-title {
    font-family: 'Oswald', sans-serif;
    margin-top: 0.8rem;
    color: #404040;

}

.card-title span {
    font-size: 1.5rem;
    font-weight: 900;
    margin-right: 0.5rem;
}

.card-text {
    font-family: 'Oswald', sans-serif;
    font-size: 12px;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.icons {
    margin-top: auto;
    /* margin-right:1rem; */
    color: #404040;
    font-size: 2rem;

}